@import url('https://fonts.googleapis.com/css2?family=Lilita+One&family=Martian+Mono:wght@600&family=Play:wght@700&display=swap');


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Set body styles */
body {
    font-family: Arial, sans-serif;
    height: 1000px;
    overflow-y: scroll;
    display: grid;
    /* padding: 19px; */
    /* background-image: url(./waves-in-norways-lofoten-islands-fz.jpg); */
    /* background-repeat: no-repeat;
    background-size: cover; */
}
.top{
    height: 15%;
    display: grid;
    width: 100%;
    grid-gap: 0px;
    grid-template-columns: 1fr 2fr 4fr;/* LISTEN FUTURE VIVEK CHECKING IT..ITS A COLUMNS CONTENT DEVIVEDER WITH EQUAL DRISTRIBUTION OF SPACE LIKE 1fr MEANS 1PART AND OTHERS WITH MORE HAVING 2fror more VALUE */
    align-items: center;
    padding: 5px;
    /* border-radius: 20px; */
    position: fixed;
    padding: 1%;
    box-shadow: none;
    background-color: azure;
}
.top:hover{
    /* box-shadow: 5px 5px 15px rgb(36, 48, 76); */
    background-color: inherit;
    transition: all 0.2s ease-in-out;
    box-shadow: 5px 5px 15px rgb(36, 48, 76);

}
.top.scrolled{
    transition: all 0.9s;
    /* background-image: url(./snowy-winter-morning-landscape-4k-fk.jpg); */
    position: fixed;
}
.top.scrolled h1{
    color: black;
    text-shadow: 3px 3px 2px rgb(136, 134, 134);
}

.profimg{
    height: 100%;
    width: 100px;
    border-radius: 15%;
    border: 1px solid;
}
.viv{
    font-family: 'Play';
    color: rgb(0, 0, 0);
    text-shadow: 3px 3px 2px rgb(255, 255, 255);
    font-weight: 700;
    margin-left: -20%;
    font-size: 35px;
} 
