.info{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.mennu{
    /* justify-content: space-between; */
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding-right: 10%;
}

.mennu h1 a{
    text-decoration: none;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 18px;
    color: rgb(29, 28, 28);
    padding: 10%;
    border-radius: 10px;
    background-color: azure;
}
.mennu h1 a:hover{
color: rgb(29, 28, 28);
    padding: 9%;
    border-radius: 10px;
    /* background-color: rgb(219, 219, 219);} */}
.first{
    height: 600px;
    margin-top: 8%;
    background-image: url(./waves-in-norways-lofoten-islands-fz.jpg);
    background-repeat: no-repeat;
    background-size:cover;
    width: 100%;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 1%;
}
.first h1{
    font-family: 'Martian Mono';
    border: 4px solid;
    font-size: 60px;
    color: black;
    background-color: inherit;
}
.app{
    height: 9000px;
    background-color: azure;
    display: flex;
}